import React from 'react';

interface Props {
  className?: string;
  icon: 'file-text' | 'comment-2-question' | 'phone' | 'user-square';
  label: string;
  callback?: any;
}

const WorkbookItem: React.FC<Props> = (props) => {
  const { className = '', icon, label, callback = () => {} } = props;

  return (
    <div className={`hb-workbook-item ${className}`} onClick={callback}>
      <div className="d-flex align-items-center" style={{ width: 'calc(100% - 30px)' }}>
        <div className="icon">
          <i className={`hb-icon-${icon}`} />
        </div>
        <div className="label">{label}</div>
      </div>
      <div className="arrow">
        <i className="hb-icon-arrow-right" />
      </div>
    </div>
  );
};

export default WorkbookItem;
