import React from 'react';
import { Link, useHistory } from 'react-router-dom';
import config from '../../../config';
import { SUCCESS } from '../../../store/callApi/actions';
import { createCategoryHashtag, defaultTrainer, videoNoThumbnail } from '../../../extensions';
import { intToString } from '../../../utils';
import Tippy from '@tippyjs/react';
import 'tippy.js/dist/tippy.css';
import { useApi } from 'store/callApi';
import { KMSDiv } from 'components';

interface Props {
  index: number;
  id: string;
  thumbnail: string;
  created_date: number;
  title: string;
  viewed_amount: number;
  setVideos: (videos: any) => void;
  is_saved?: boolean;
  screenSaved?: boolean;
  categories: { id: string; name: string }[];
  isRelated?: boolean;
  isDetail?: boolean;
  category_name?: string;
  trainer_avatar: string;
  trainer_id: string;
  className?: string;
  setLibrary?: any;
}

const MobilityVideo: React.FC<Props> = (props) => {
  const { callApi } = useApi();

  const {
    index,
    id,
    thumbnail,
    title,
    viewed_amount,
    setVideos,
    is_saved,
    screenSaved,
    isRelated,
    isDetail,
    category_name,
    trainer_avatar,
    trainer_id,
    className,
    setLibrary,
  } = props;

  const history = useHistory();

  const gotoTrainer = (e: any) => {
    e.preventDefault();
    if (!trainer_id) return;
    history.push(`/mobility/trainer-detail/${trainer_id}`);
  };

  const saveAction = (e: any) => {
    e.preventDefault();
    if (!screenSaved)
      setVideos((videos: any) => {
        let newVideos = [...videos.data];
        newVideos[index].is_saved = !is_saved;
        return { ...videos, data: newVideos };
      });
    else
      setVideos((videos: any) => {
        let newVideos = videos.data.filter((newVideo: any) => newVideo.id !== id);
        return { ...videos, data: newVideos };
      });

    setLibrary &&
      setLibrary((library: any) => {
        if (!library) return null;
        return { ...library, is_save_number: library.is_save_number + (is_saved ? -1 : 1) };
      });

    callApi(
      {
        method: !is_saved ? 'post' : 'put',
        api: !is_saved ? config.rest.saveVideo(id) : config.rest.unsaveVideo(id),
      },
      (response) => {
        const { status } = response;
        if (status === SUCCESS) {
        }
      },
    );
  };

  return (
    <div
      className={`${
        isRelated ? 'hb-card-item' : isDetail ? 'col-md-6 hb-card-item' : 'col-lg-4 col-md-6 hb-card-item'
      } ${className}`}
      style={{ marginBottom: isRelated ? 0 : 16, marginTop: isRelated ? 16 : 0 }}
    >
      <Link to={`/mobility/watch?video=${id}`}>
        <KMSDiv
          backgroundImage={thumbnail || videoNoThumbnail}
          className="Video-thumbnail"
          style={{
            overflow: 'hidden',
            width: '100%',
            display: 'block',
            borderRadius: '32px 32px 0 0',
            height: 182,
            backgroundSize: 'cover',
          }}
        />
        <div
          style={{
            backgroundColor: 'white',
            padding: '20px',
            borderRadius: '0 0 30px 30px',
            border: isRelated ? '1px solid var(--petrol-5)' : 'none',
          }}
        >
          <div className="d-flex justify-content-between">
            <div className="d-flex align-items-center">
              <KMSDiv
                backgroundImage={trainer_avatar || defaultTrainer}
                onClick={gotoTrainer}
                style={{
                  cursor: trainer_id ? 'pointer' : 'default',
                }}
                className="hb-card-avatar"
              />
              <Tippy interactive={true} content={viewed_amount || '0'}>
                <div className="hb-card-views-wrapper">
                  <i className="hb-icon-eye" style={{ color: 'var(--violet-80)', fontSize: 22, marginRight: 7 }} />
                  <span className="small">{intToString(viewed_amount)}</span>
                </div>
              </Tippy>
            </div>
            <div className="hb-save" onClick={saveAction}>
              <i className={`${is_saved ? 'hb-icon-heart-s' : 'hb-icon-heart-r'}`} />
            </div>
          </div>
          <h4 className="hb-card-title video">{title}</h4>
          <div className="d-flex" style={{ marginTop: 10 }}>
            {createCategoryHashtag(category_name || '')}
          </div>
        </div>
      </Link>
    </div>
  );
};

export default MobilityVideo;
