import React, { useEffect, useRef, useState } from 'react';
import config from '../../../config';
import { SUCCESS } from '../../../store/callApi/actions';
import { defaultAvatar } from '../../../extensions';
import { useSelector } from 'react-redux';
import { HBButtonFull, HBButtonIcon } from '../../../hbBaseClass';
import InputLoading from './InputLoading';
import { useApi } from 'store/callApi';
import { KMSImg } from 'components';

interface Props {
  videoId: string;
  commentId?: string;
  commentContent?: string;
  setComments: (params: any) => void;
  setShowUpdateInput?: (params: any) => void;
}

const CommentInput: React.FC<Props> = (props) => {
  const { callApi } = useApi();

  const [loading, setLoading] = useState(false);
  const { videoId, commentId, commentContent, setComments, setShowUpdateInput } = props;
  const [content, setContent] = useState(commentId ? commentContent : '');
  const auth = useSelector((state: any) => state.auth);
  const avatar = auth.avatar || defaultAvatar;

  const inputRef = useRef<HTMLInputElement>(null);
  useEffect(() => {
    commentId && inputRef.current?.focus();
  }, []);

  const createComment = () => {
    if (!content?.trim()) return;
    setLoading(true);
    callApi(
      {
        method: 'post',
        api: config.rest.createComment(videoId),
        body: { comment: content },
      },
      (response: any) => {
        setLoading(false);
        const { data, status } = response;
        if (status === SUCCESS) {
          setContent('');
          setComments((comments: any) => {
            return {
              data: [
                {
                  id: data.id,
                  user_id: auth.user_id,
                  user_avatar: avatar,
                  username: auth.first_name + ' ' + auth.last_name,
                  content: content,
                  liked_amount: 0,
                  is_liked: false,
                  replied_amount: 0,
                  created_date: new Date().getTime() / 1000,
                },
                ...comments.data,
              ],
              total: comments.total + 1,
            };
          });
        }
      },
    );
  };

  const updateComment = () => {
    if (!content?.trim()) return;
    setLoading?.(true);
    callApi(
      {
        method: 'put',
        api: config.rest.updateComment(videoId, commentId || ''),
        body: { comment: content },
      },
      (response: any) => {
        setLoading?.(false);
        setShowUpdateInput?.(false);
        const { status } = response;
        if (status === SUCCESS) {
          setComments((comments: any) => {
            const commentsData = [...comments.data];
            const index = commentsData.findIndex((item) => item.id === commentId);
            const commentData = commentsData[index];
            commentData.content = content;
            return {
              ...comments,
              data: commentsData,
            };
          });
        }
      },
    );
  };

  const handleKeyDown = (event: any) => {
    if (event.key === 'Enter') {
      if (commentId) updateComment();
      else createComment();
    }
  };

  if (loading) return <InputLoading />;

  return (
    <div>
      <div className="hb-comment-input">
        <KMSImg className="hb-avatar-img" alt="avatar" src={avatar} />
        <div className="input-wrapper">
          <input
            ref={inputRef}
            placeholder="Ihr Kommentar ..."
            value={content}
            onChange={(e: any) => setContent(e.target.value)}
            onKeyDown={handleKeyDown}
            maxLength={500}
            style={{ marginRight: commentId ? 0 : 8 }}
          />
          {content && (
            <HBButtonIcon
              style={{ position: 'absolute', right: commentId ? 16 : 24 }}
              circle={{ color: '#dddedf', size: 24 }}
              icon="cross"
              size={16}
              color="var(--violet-80)"
              onClick={() => setContent('')}
            />
          )}
        </div>
        {!commentId && (
          <HBButtonIcon
            icon="send"
            size={24}
            color="var(--violet-80)"
            disabled={content?.trim() === ''}
            onClick={commentId ? updateComment : createComment}
          />
        )}
      </div>
      {commentId && (
        <div className="d-flex justify-content-end mt-3">
          <div className="hb-btn-comment-wrapper mr-2">
            <HBButtonFull
              outline
              small
              // disabled={commentId ? false : content === ''}
              onClick={() => (commentId ? setShowUpdateInput?.(false) : setContent(''))}
            >
              Abbrechen
            </HBButtonFull>
          </div>
          <div className="hb-btn-comment-wrapper">
            <HBButtonFull
              small
              disabled={!content?.trim() || content?.trim() === commentContent}
              onClick={commentId ? updateComment : createComment}
            >
              Speichern
            </HBButtonFull>
          </div>
        </div>
      )}
    </div>
  );
};

export default CommentInput;
